import React from 'react';
import { HomepageBanner, HomepageCallout } from 'gatsby-theme-carbon';
import HomepageTemplate from 'gatsby-theme-carbon/src/templates/Homepage';
import { calloutLink, bannerText } from './Homepage.module.scss';

import Carbon from '../../images/popup-2022.jpeg';

const FirstLeftText = () => <p>Instana Lab</p>;

const FirstRightText = () => (
  <p>
    In this lab you will learn how easy observability can be with{' '}
    <a href="https://www.instana.com/">
      Instana
    </a>
    . You will also discover how to use the <code>UI</code> and <code>Agents</code>{' '}
    with your envrionment and applications.
    <a
      className={calloutLink}
      href="https://www.instana.com/">
      Instana Homepage 
    </a>
  </p>
);

const SecondLeftText = () => <p></p>;

const SecondRightText = () => (
  <p>
  </p>
);

const BannerText = () => <h1 className={bannerText} >IBM Think Labs</h1>;

const customProps = {
  Banner: <HomepageBanner renderText={BannerText} image={Carbon} />,
  FirstCallout: (
    <HomepageCallout
      backgroundColor="white"
      color="#525252"
      leftText={FirstLeftText}
      rightText={FirstRightText}
    />
  ),
  SecondCallout: (
    <HomepageCallout
      backgroundColor="white"
      color="#525252"
      leftText={FirstLeftText}
      rightText={FirstRightText}
    />
  ),
};

// spreading the original props gives us props.children (mdx content)
function ShadowedHomepage(props) {
  return <HomepageTemplate {...props} {...customProps} />;
}

export default ShadowedHomepage;
